}

/* ÁõÆÁ´ã„Å§„É≠„Ç∞„Ç¢„Ç¶„Éà„Éú„Çø„É≥Ôºàbtn-mini „Çí‰∏äÊõ∏„ÅçÔºèÂøÖ„Åö„Åì„ÅÆ„Éñ„É≠„ÉÉ„ÇØ„Çí‰∏ã„Å´ÁΩÆ„ÅèÔºâ */
